.footer{
    padding: 10px 30px;
    height: 80px;
    background-color: var(--main-color-bold);
    border-radius: 25px 25px 0px 0px;
}
.form{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.input{
    height: 58px;
    padding: 10px 20px;
    width: 90%;
    background: var(--main-color-light);
    border-radius: 25px;

    font-size: 30px;
    line-height: 1.5;
    color: #FFFFFF;
    margin-right: 15px;
}
.button{
    background-color: var(--main-color-light);
    height: 58px;
    width: 58px;
    border-radius: 25px;
}