.chat{
    display: flex;
    flex-direction: column;
    position: relative;
    max-width: 728px;
    margin: 0 auto;
    background-color: var(--bgcolor-light);
    height: 100vh;
}
.header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    background-color: var(--main-color-bold);
    border-radius: 0px 0px 25px 25px;
    opacity: .5;
}
